module.exports = "<h2 id=metadata>Metadata</h2> <p>Metadata – including title, subtitle, caption, and credit – can be entered in the <strong>Data Input</strong> (title only) or <strong>Settings</strong> steps. These fields will be displayed when the chart is embedded in a post. The subtitle field is disabled by default and can be enabled by site-specific setup.</p> <p>Although this metadata is optional <em>for Simplechart</em>, your CMS (e.g. WordPress) may require a title before saving the chart in the database. In this case, you may need to enter a post title directly in the CMS before saving the chart, if you have not already added a title in the Simplechart app.</p> ";

