module.exports = "<h2 id=load-data-from-a-google-sheet>Load Data From a Google Sheet</h2> <h3 id=notes>Notes</h3> <ul> <li>A Google Sheets API key integrated into your CMS is required.</li> <li>Data must begin in Column A, Row 1.</li> <li>The spreadsheet will be parsed by rows.</li> <li>The spreadsheet must be publicly available.</li> </ul> <h3 id=make-spreadsheet-public>Make Spreadsheet Public</h3> <ol> <li>Within your spreadsheet click the blue share button on the top right corner.</li> <li>Within the &quot;Share with others&quot; modal click the advanced link on the bottom right corner.</li> <li>Under &quot;Who has access&quot; click the Change... link</li> <li>Select &quot;On - Public on the web&quot; then click save.</li> </ol> ";

